.storybox{
    width: 100%;
    display: flex;
    flex-wrap:wrap;
    justify-content: space-between;
    .onestory{
        width: 48%;
        margin-bottom: 15px;
        img{
            width: 100%;
            height: 3.3rem;
            border-radius: 7px;
        }
        .story_title{
            font-size: 0.28rem;
            overflow: hidden;
            text-overflow: ellipsis;
            margin: 0.1rem 0;
            span{
                font-weight: bold;
                font-size: 0.26rem;
                margin-right: 0.1rem;
                line-height: 0.4rem;
            }
        }
        .story_info{
            display: flex;
            align-items: center;
            justify-content: space-between;
            font-size: 0.24rem;
            span{
                display: flex;
                align-items: center;
                img{
                    width: 0.45rem;
                    height: 0.45rem;
                    border-radius:50%;
                    margin-right: 0.1rem;
                }
                i{
                    font-size: 0.4rem;
                    font-weight: bold;
                }
                b{
                    font-size: 0.26rem;
                    font-weight: normal;
                }
            }
        }
    }
    button{
        width: 100%;
        margin-top: 0.2rem;
        border: 1px solid #008489;
        line-height: 0.6rem;
        background-color: #fff;
        font-size: 0.28rem;
        color: #008489;
        font-weight: bold;
        border-radius: 5px;
    }
}